body {
  padding: 0;
  margin: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
*,
*::after,
*::before {
  box-sizing: border-box;
}
:root {
  --color1: #9eb0a2;
  --color2: #0089c5;
  --color3: #5ec280;
  --text1: #343a36;
}
body {
  background-color: var(--color1);
  color: var(--text1);
}
body h1 {
  padding-left: 1rem;
  opacity: 0.5;
}
body p,
h2 {
  text-align: center;
}
h2 {
  color: var(--color2);
}
form {
  max-width: 400px;
  display: flex;
  justify-content: center;
  margin: 1rem auto;
  flex-direction: column;
  align-items: center;
}
form div {
  width: 100%;
}
.error {
  color: rgb(213, 87, 87);
  font-size: 0.8rem;
}
label input {
  display: none;
}
label {
  background-color: var(--color2);
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
}
.progressbar {
  background-color: var(--color2);
  height: 5px;
}
.image-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 0.5rem;
  max-width: 700px;
  margin: 0 auto;
}
.image-grid .image-wrapper {
  width: 100%;
  height: 250px;
}
.image-grid .image-wrapper img {
  border-radius: 10px;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.backdrop {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: center;
  align-items: center;
}
.backdrop img {
  max-width: 80%;
  max-height: 70%;
}

.download-icon {
  width: 50px;
  position: absolute;
  bottom: 0;
  right: 0;
}
@media screen and (max-width: 760px) {
  .image-grid {
    padding: 1rem;
  }
  .title h1 {
    text-align: center;
  }
}
